@use "@tcds" as *;

details {
  &:where(:not(:last-child)) {
    margin-bottom: 1em;
  }
}

summary {
  display: flex;
  gap: space("tight");
  align-items: center;
  list-style: none;
  background: color("gray", "0");
  color: color("gray", "400");
  font-size: font-size("small");
  font-weight: font-weight("semibold");
  line-height: line-height("loose");
  padding: space("tight") space("normal");
  user-select: none;
  width: 100%;
  cursor: pointer;
  transition:
    background animation("productive"),
    color animation("productive");

  &:hover {
    background: theme-color("secondary");
    color: theme-color("secondary", "0");

    --icon-filter: invert(1);
  }

  [open] > & {
    --icon-rotate: 90deg;
  }

  &::-webkit-details-marker {
    display: none;
  }

  &::before {
    content: "";
    width: 1em;
    height: 1em;
    background-image: url("#{$ASSETS}/icons/chevron-right.svg");
    background-size: 100%;
    display: block;
    transition:
      transform animation("productive"),
      filter animation("productive");
    filter: var(--icon-filter, none);
    transform: rotateZ(var(--icon-rotate, 0));
  }

  + div {
    padding: space("normal");
  }
}
